	var config = {};

	//私有云切换navi导航，私有云格式 '120.92.10.214:8888'
	if (navi !== "") {
		config.navi = navi;
	}

	//私有云切换api,私有云格式 '172.20.210.38:81:8888'
	var api = params.api || "";
	if (api !== "") {
		config.api = api;
	}

	//support protobuf url + function
	if (protobuf != null) {
		config.protobuf = protobuf;
	};

	var dataProvider = null;
	var imClient = params.imClient;
